@import "https://fonts.googleapis.com/css2?family=Boldonse&display=swap";
:root {
  --primary-color: #21344b;
  --secondary-color: #78b9ba;
  --text-color: #333;
  --background-color: #c6c5b4;
  --accent-color: #ce472f;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, Arial, sans-serif;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
}

header {
  background-color: var(--primary-color);
  color: #fff;
  text-align: center;
  padding: 1rem 0 0%;
  box-shadow: 0 2px 5px #0000001a;
}

.banner-content {
  width: 100%;
  padding-top: 5%;
  overflow: hidden;
}

.banner-content img {
  object-fit: cover;
  width: 100%;
  height: auto;
  max-height: 400px;
  display: block;
}

.logo {
  color: var(--accent-color);
  margin-bottom: .5rem;
  font-family: Boldons;
  font-size: 4rem;
  font-weight: bold;
}

nav {
  background-color: var(--secondary-color);
  padding: 1rem 0;
}

nav ul {
  justify-content: center;
  list-style-type: none;
  display: flex;
}

nav ul li {
  margin: 0 1rem;
}

nav ul li a {
  color: var(--text-color);
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

nav ul li a:hover {
  color: var(--primary-color);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.hero {
  text-align: center;
  margin-bottom: 3rem;
}

.hero h1 {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.hero p {
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.2rem;
}

.featured-recipes {
  margin-bottom: 3rem;
}

.section-title {
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 2rem;
}

.recipe-grid {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  display: grid;
}

.recipe-card {
  background-color: #fff;
  border-radius: 10px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 3px 10px #0000001a;
}

.recipe-card:hover {
  transform: translateY(-5px);
}

.recipe-image {
  background-color: #ddd;
  background-position: center;
  background-size: cover;
  height: 200px;
}

.recipe-content {
  padding: 1.5rem;
}

.recipe-title {
  color: var(--primary-color);
  margin-bottom: .5rem;
  font-size: 1.3rem;
}

.recipe-movie {
  color: var(--accent-color);
  margin-bottom: .5rem;
  font-style: italic;
}

.recipe-description {
  margin-bottom: 1rem;
}

.recipe-link {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: 5px;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.recipe-link:hover {
  background-color: var(--accent-color);
}

.categories {
  margin-bottom: 3rem;
}

.category-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.category-item {
  background-color: var(--secondary-color);
  cursor: pointer;
  border-radius: 20px;
  padding: .7rem 1.5rem;
  transition: background-color .3s;
}

.category-item:hover {
  background-color: var(--primary-color);
  color: #fff;
}

.detalle-receta {
  background-color: #fff;
  border-radius: 8px;
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem;
  box-shadow: 0 2px 10px #0000001a;
}

.detalle-receta img {
  border-radius: 8px;
  width: 50%;
  height: auto;
  margin-bottom: 2rem;
}

.detalle-receta .recipe-subtitle {
  padding-right: 2rem;
}

.detalle-receta .header-receta {
  display: flex;
}

.detalle-receta .movie-reference {
  background-color: #78b9ba;
  border-left: 4px solid #21344b;
  border-radius: 8px;
  margin: 2rem 0;
  padding: 1.5rem;
}

.detalle-receta .movie-reference img {
  float: right;
  border-radius: 4px;
  width: 120px;
  height: auto;
  margin: 0 0 1rem 1rem;
}

.ingredients-section, .preparation-section {
  margin: 2rem 0;
}

.ingredients-list {
  padding-left: 0;
  list-style-type: none;
}

.ingredients-list li {
  border-bottom: 1px dashed #eee;
  justify-content: space-between;
  padding: .5rem 0;
  display: flex;
}

.ingredients-list span.cantidad {
  color: #ce472f;
  font-weight: bold;
}

.preparation-steps {
  counter-reset: step-counter;
  padding-left: 0;
  list-style-type: none;
}

.preparation-steps li {
  margin-bottom: 1.5rem;
  padding-left: 50px;
  position: relative;
}

.preparation-steps li:before {
  content: counter(step-counter);
  counter-increment: step-counter;
  color: #fff;
  background-color: #ce472f;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  font-weight: bold;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.nutrition-info {
  background-color: #f9f9f9;
  border-radius: 8px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 2rem 0;
  padding: 1.5rem;
  display: flex;
}

.nutrition-info .nutrition-item {
  text-align: center;
  flex: 1;
  min-width: 100px;
  padding: 1rem;
}

.nutrition-info .nutrition-item .nutrition-value {
  color: #d35400;
  font-size: 1.5rem;
  font-weight: bold;
}

.nutrition-info .nutrition-item .nutrition-label {
  color: #666;
  font-size: .9rem;
}

.related-recipes {
  margin: 3rem 0;
}

.related-recipes .recipe-cards {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  display: grid;
}

.related-recipes .recipe-cards .recipe-card-small {
  border-radius: 8px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 2px 5px #0000001a;
}

.related-recipes .recipe-cards .recipe-card-small:hover {
  transform: translateY(-5px);
}

.related-recipes .recipe-cards .recipe-card-small img {
  object-fit: cover;
  width: 100%;
  height: 150px;
  margin-bottom: 0;
}

.related-recipes .recipe-cards .recipe-card-small .recipe-card-content {
  padding: 1rem;
}

.related-recipes .recipe-cards .recipe-card-small .recipe-card-content .recipe-card-title {
  margin: 0;
  font-size: 1.1rem;
}

.related-recipes .recipe-cards .recipe-card-small .recipe-card-content .recipe-card-movie {
  color: #777;
  margin: .5rem 0;
  font-size: .9rem;
}

footer {
  background-color: var(--primary-color);
  color: #fff;
  text-align: center;
  margin-top: 3rem;
  padding: 2rem 0;
}

.social-icons {
  margin-top: 1rem;
}

.social-icons span {
  margin: 0 .5rem;
  font-size: 1.5rem;
}

@media (width <= 768px) {
  .recipe-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  nav ul {
    flex-direction: column;
    align-items: center;
  }

  nav ul li {
    margin: .5rem 0;
  }

  .detalle-receta .movie-reference img {
    float: none;
    width: 150px;
    margin: 0 auto 1rem;
    display: block;
  }

  .newsletter form {
    flex-direction: column;
  }

  .newsletter form input {
    border-right: 1px solid;
    border-radius: 5px;
    margin-bottom: .5rem;
  }

  .newsletter form button {
    border-radius: 5px;
  }
}

@media (width >= 600px) {
  .blog-read-time-bar {
    height: .5rem;
  }
}

.blog-read-time-bar {
  width: 100%;
  height: .25rem;
  display: none;
  position: absolute;
}

.blog-read-time-bar.-active {
  display: block;
}

.blog-read-time-bar-indicator {
  appearance: none;
  height: inherit;
  pointer-events: none;
  width: 100%;
  height: inherit;
  pointer-events: none;
  background-color: #0000;
  border: 0;
  width: 100%;
  position: absolute;
}

progress {
  vertical-align: baseline;
  appearance: auto;
  box-sizing: border-box;
  vertical-align: -.2em;
  block-size: 1em;
  inline-size: 10em;
  display: inline-block;
}

.blog-read-time-bar-indicator::-moz-progress-bar {
  background-color: #ff5c35;
}

.blog-read-time-bar-indicator::-webkit-progress-bar {
  background-color: #f6f9fc;
}

.blog-read-time-bar-indicator::-webkit-progress-value {
  background-color: #ff5c35;
}

.blog-read-time-bar-indicator.-the-hustle::-webkit-progress-value {
  background-color: #bf2434;
}

.blog-read-time-bar-indicator.-the-hustle::-moz-progress-bar {
  background-color: #bf2434;
}

.blog-read-time-bar-indicator:focus {
  outline: none;
}
